<template>
  <tr>
    <td>{{ bno }}</td>
    <td>
      <router-link :to="`board/view?bno=${bno}`">{{ title }}</router-link>
    </td>
    <td>{{ author }}</td>
  </tr>
</template>

<script>
export default {
  name: "listrow",
  props: {
    bno: String,
    title: String,
    author: String,
  },
  methods: {
    numberWithCommas(x) {
      return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    }
  }
};
</script>
<style scope>
td {
  text-align: center;
  border-bottom: 1px solid #ddd;
  height: 50px;
}
</style>
