<template>
  <div>
    <h1 class="underline">QnA 게시판</h1>
    <div style="text-align: right">
      <button @click="movePage">게시글 등록</button>
    </div>
    <div v-if="boards.length">
      <table id="board-list">
        <colgroup>
          <col style="width: 20%" />
          <col style="width: 60%" />
          <col style="width: 20%" />
        </colgroup>
        <thead>
          <tr>
            <th>게시글 번호</th>
            <th>제목</th>
            <th>작성자</th>
          </tr>
        </thead>
        <tbody>
          <list-row
            v-for="(board, index) in boards"
            :key="index"
            :bno="board.bno"
            :title="board.title"
            :author="board.author"
          />
        </tbody>
      </table>
    </div>
    <div v-else class="text-center">게시글이 없습니다.</div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import ListRow from "@/components/board/include/ListRow.vue";

export default {
  name: "boardlist",
  components: {
    ListRow
  },
  computed: {
    ...mapGetters(["boards"])
  },
  created() {
    this.$store.dispatch("getBoards");
  },
  methods: {
    movePage() {
      this.$router.push({ name: "board-create" });
    }
  }
};
</script>

<style scoped>
#board-list {
  border-collapse: collapse;
  width: 100%;
}

#board-list thead {
  background-color: #ccc;
  font-weight: bold;
}

#board-list td,
#board-list th {
  text-align: center;
  border-bottom: 1px solid #ddd;
  height: 50px;
}

tr:nth-child(even) {
  background-color: #f2f2f2;
}
</style>
